import {
	Scene,
	PerspectiveCamera,
	WebGLRenderer,
	Clock,
	TextureLoader,
	MeshBasicMaterial,
	DoubleSide,
	ACESFilmicToneMapping,
	RepeatWrapping,
	CatmullRomCurve3,
	Vector3,
	TubeGeometry,
	Mesh,
	CircleGeometry,
	AdditiveBlending,
} from "three";

const textureLoader = new TextureLoader();

class World {
	scene = new Scene();

	camera = new PerspectiveCamera(
		50,
		window.innerWidth / window.innerHeight,
		0.1,
		1000
	);

	renderer = new WebGLRenderer();

	clock = new Clock();

	texture = textureLoader.load("./assets/bg.png");

	texture2 = textureLoader.load("./assets/texture.png");

	material = new MeshBasicMaterial({
		transparent: true,
		map: this.texture,
		side: DoubleSide
	});

	triangles = [];

	constructor() {
		this._init();
		this._initTexture();
		this._createCurve();
		this._createTriangles();
	}

	_init() {
		this.renderer.shadowMap.enabled = true;
		this.renderer.toneMapping = ACESFilmicToneMapping;
		this.renderer.setSize(window.innerWidth, window.innerHeight);
		document.body.appendChild(this.renderer.domElement);

		this.camera.position.set(0, 0, 0);

		window.addEventListener("resize", () => {
			this.camera.aspect = window.innerWidth / window.innerHeight;
			this.camera.updateProjectionMatrix();
			this.renderer.setSize(window.innerWidth, window.innerHeight);
			this.renderer.setPixelRatio(window.devicePixelRatio);
		});
	}

	_initTexture() {
		this.texture.wrapS = RepeatWrapping;
		this.texture.wrapT = RepeatWrapping;
		this.texture.repeat.set(1, 2);
	}

	_createCurve() {
		this.curve = new CatmullRomCurve3([
			new Vector3(-10, 0, 10),
			new Vector3(-5, 5, 5),
			new Vector3(0, 0, 0),
			new Vector3(5, -5, 5),
			new Vector3(10, 0, 10)
		], true);

		const geometry = new TubeGeometry(this.curve, 128, 1, 8, true);

		const mesh = new Mesh(geometry, this.material);
		this.scene.add(mesh);
	}

	_createTriangles() {
		const geometry = new CircleGeometry(Math.random() * 0.2, 0, 0, Math.PI * 2);
		const material = new MeshBasicMaterial({
			map: this.texture2,
			alphaMap: this.texture2,
			transparent: true,
			depthWrite: false,
			blending: AdditiveBlending,
			side: DoubleSide
		});
		for (let i = 0; i < 1000; i++) {
			const pos = this.curve.getPointAt(i * 0.001);
			const tri = new Mesh(
				geometry,
				material
			);
			tri.rotation.set(Math.random() * 3, Math.random() * 3, Math.random() * 3);
			tri.position.copy(pos);
			tri.translateZ(Math.random() * 4);
			tri.translateX(Math.random() * 4);
			tri.translateY(Math.random() * 4);

			this.scene.add(tri)
			this.triangles.push(tri)
		}
	}

	render() {
		this.renderer.setAnimationLoop(() => {
			const elapsed = this.clock.getElapsedTime();
			const timeLine = (elapsed * 0.1) % 1;
			if (this.curve) {
				const point = this.curve.getPointAt(timeLine);
				const nextPoint = this.curve.getPointAt((timeLine + 0.05) % 1);
				this.camera.position.copy(point);
				this.camera.lookAt(nextPoint.x, nextPoint.y, nextPoint.z);

				this.triangles.forEach(plane => {
					plane.rotation.x += 0.02
					plane.rotation.y += 0.02
				})

				this.texture.offset.x += 0.0005

				this.renderer.render(this.scene, this.camera);
			}
		});
	}
}

const world = new World();
world.render()
